@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;0,700;1,500;1,700&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

@import "config";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  min-height: 100%;
  scroll-behavior: smooth;
  font-family: "Poppins", sans-serif;
}

.burger > div {
  width: 25px;
  height: 2px;
  margin: 5px 0;
  background-color: $grayishViolet;
}
